// Local $ reference (in React) to global $ (jQuery) from public/index.html
var $ = window.$;

// onDomReady...
var handleReady = () => {
  var squares = $( '.square' ),
      board = $( '.game-board' ),

      // onResize...
      handleResize = () => {
        board.width( $(window).height() - 50 );
        squares.width( (board.width() / 3) - 4 );
        squares.height( squares.width() );
      };

  $( window ).resize( handleResize );
  handleResize(); // call handleResize once on page load

  squares.fitText(0.1);
}

$( document ).ready( handleReady );



